<template>
  <section class="rightlist-wdqb">
    <div class="part1">
      <h2>我的钱包</h2>
      <div class="allmoney">
        <div class="all">总金额</div>
        <div class="left">
          <p class="sz"><span>2564</span>元</p>
          <p class="tips">总金额包含可用金额和提现中的金额</p>
        </div>
        <div class="right">
          <a href="javascript:;">提现</a>
        </div>
        <div class="clear"></div>
      </div>
      <div class="usemoney">
        <p>可用金额<strong>2564</strong>元</p>
        <p>提现中金额<strong>365</strong>元</p>
      </div>
    </div>

    <div class="part2">
      <div class="table">
        <table>
          <tr>
            <th><span>时间</span></th>
            <th><span>交易金额(元)</span></th>
            <th><span>对应账号</span></th>
            <th><span>交易类型</span></th>
            <th><span>交易状态</span></th>
            <th><span>操作</span></th>
          </tr>
          <tr>
            <td>2018-11-11 17:12:45</td>
            <td>1896.69</td>
            <td>132****5697</td>
            <td>充值</td>
            <td>交易中</td>
            <td>--</td>
          </tr>
          <tr>
            <td>2018-11-11 17:12:45</td>
            <td>36.35</td>
            <td>132****5697</td>
            <td>提现</td>
            <td>成功</td>
            <td>--</td>
          </tr>
          <tr>
            <td>2018-11-11 17:12:45</td>
            <td>15.69</td>
            <td>132****5697</td>
            <td>充值</td>
            <td>失败</td>
            <td>查看原因</td>
          </tr>
        </table>
      </div>
      <div class="page">
        <div class="left">
          <a href="javascript:;">1</a>
          <a href="javascript:;" class="updown">下一页</a>
        </div>
        <div class="right">
          <p>共3条</p>
          <p>前往<input type="text">页</p>
        </div>
        <div class="clear"></div>
      </div>
    </div>


  </section>
</template>
<script>
export default {
  name: 'ucwallet',
  data:{

  },
  methods:{
    
  }
}
</script>
<style lang="scss" scoped>
section.rightlist-wdqb{
  float: left;
  .active{
    color: #EA6457;
  }
  .part1{
    margin:20px 0 20px 14px;
    padding:0 27px;
    width:900px;
    height: 317px;
    box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
    background-color:#fefefe;
    opacity: 0.93;
    border-radius: 5px;
    h2{
      border-bottom:1px solid #dcdcdc;
      font-size: 28px;
      color: #6c6c6c;
      padding:27px 0 30px 0;
    }
    
    .allmoney{
      // height: 65px;

      padding-bottom:10px;
      line-height: 40px;
      border-bottom:1px solid #dcdcdc;
      .all{
        padding:15px 0 0 0;
        color: #9199a2;
        font-size:16px;
      }
      .left{
        float: left;
        height: 66px;
        line-height: 50px;
        p{
          display: inline-block;
        }
        p.sz{
          height:35px;
          font-size:16px;
          color:#EA6457;
          padding:0 25px 0 5px;
          border-right:1px solid #e9e9e9;
          span{
            margin-right:5px;
            vertical-align: baseline;
            font-size: 40px;
          }
        }
        p.tips{
          height: 28px;
          font-size: 12px;
          color:#bfbfbf;
          padding-left:40px;
        }

      }
      .right{float: right;
        a{
          width:120px;
          height: 40px;
          line-height: 40px;
          font-size:16px;
          text-align: center;
          color: #fff;
          background-color:#EA6457;
          display: inline-block;
          border-radius: 5px;
        }
      }
    }
    .usemoney{
      p{
        display: inline-block;
        color: #6c6c6c;
        font-size:14px;
        margin:40px 70px 0 5px;
        letter-spacing: 1px;
        strong{
          vertical-align: baseline;
          font-weight: bold;
          font-size:22px;
        }
      }
    }
  }
  .part2{
    margin:20px 0 20px 14px;
    padding:35px 27px;
    width:900px;
    min-height: 450px;
    box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
    background-color:#fefefe;
    opacity: 0.93;
    border-radius: 5px;
    .table{min-height: 500px;
      table{
        width:100%;
        tr{
          height:80px;
          border-bottom:1px solid #f2f2f2;
          td{
            line-height: 22px;
            text-align: left;
            font-size:14px;
            font-weight: 400;
            padding:20px 0 0 33px;
            &:last-child{
              color: #EA6457;
            }
          }
          &:nth-child(1){
            height: 50px;
            line-height: 50px;
            border:none;
            background-color: #f1f1f1;
            th{
              span{
                width:115px;
                display: inline-block;
                border-right:1px dashed #c8c8c8;
                color:#444;
                padding-left:33px;
                text-align: left;
                font-size: 14px;
                line-height: 28px;
                font-weight:400;

              }
              &:nth-child(1){
                width:13%;
              }
              &:last-child{
                width:10%;
                span{
                  border:none;
                }
              }
            }
          }
        }
      }
    }
    .page{margin-top:50px;
      .left{float: left;
        a{
          width:40px;
          height:38px;
          color:#fff;
          line-height: 38px;
          font-size: 14px;
          text-align: center;
          background-color: #EA6457;
          display: inline-block;
          border-radius: 5px;
        }
        .updown{
          width:74px;
          height:38px;
          color: #BABDC6;
          line-height: 38px;
          text-align: center;
          display: inline-block;
          background-color: #f5f5f5;
        }
      }
      .right{
        float: right;
        p{
          display: inline-block;
          font-size: 14px;
          color:#949494;
          margin-left:10px;

          input{
            width:45px;
            height: 25px;
            margin:0 5px;
            border:1px solid #d8dbe3;
            border-radius:5px;
          }
        }

      }
    }
  }
}
</style>



